.heading {
    text-align: center;
    font-size: 5rem;
    font-family: 'Special Elite', cursive;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    text-decoration: none;
   
}
.appHeading>a{
    color: #000;
}
.appHeading>a:hover{
    text-decoration: none; 
    color: dimgray;
}
nav ul{
    text-align: center;
    margin-top: 1%
}
nav li
{
    display: inline;

    list-style: none;
    font-size: 1.5rem;
    line-height: 10px;
    margin: 1rem;
    text-shadow: 1px 1px 1px grey;
    /* font-family: 'Dancing Script', cursive; */
    font-family: 'Special Elite', cursive;

}



nav li a{

    color:#000;
}
  
nav a:link {
    text-decoration: none;
}

nav a:visited {
    text-decoration: none;
}

@media (max-width:500px){
    .heading{
        font-size: 3.5rem;
    }
    nav li{
        font-size: 1rem;
    }
}





